@import './variables';
@import './stars';

@mixin main-font{
  font-family: Roboto, sans-serif;
}

body {
  @include main-font;

  margin: 0;
  font-style: normal;
  font-weight: 400;
}

.main {
  width: 200px;
}



.card {
  margin: 0;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid #F3F3F3;
  background: $white;
  padding: 16px;

  &__photo {
    width: 160px;
    height: 134px;
    padding: 16px 3px;
    object-fit: cover;
  }

  &__name {
    margin: 0;
    padding: 20px 0 4px;
    color: $main-accent;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
  }

  &__code-text {
    margin: 0;
    color: $secondary;
    font-size: 10px;
    line-height: 14px;
  }

  &__stars {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 0 8px;

    .rewies {
      margin: 0;
      color: $main-accent;
      font-size: 10px;
      line-height: 14px;
    }
  }

  &__price {
    padding: 16px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-title {
      margin: 0;
      color: $secondary;
      font-size: 12px;
      line-height: 18px;
    }

    &-price {
      margin: 0;
      text-align: right;
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
    }
  }

  &__link {
    box-sizing: border-box;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
    border-radius: 5px;
    background: $blue-accent;
    border: none;

    color: $white;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    text-transform: uppercase;
  }

  &__link:hover {
    border: 1px solid $blue-accent;
    color: $blue-accent;
    background-color: $white;
  }
}
